                        /* 轮播图 */
                        
                        .slider {
                            position: relative;
                            height: 450px;
                            min-width: 1519px;
                            width: 100%;
                            overflow: hidden;
                            .slider-pointer {
                                position: absolute;
                                bottom: 20px;
                                color: #fff;
                                list-style: none;
                                padding: 0;
                                z-index: 999;
                                li {
                                    display: inline-block;
                                    margin: 0 15px;
                                    width: 15px;
                                    height: 15px;
                                    border-radius: 15px;
                                    background-color: #ffffff;
                                    opacity: 0.85;
                                    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.5);
                                    transition: all 320ms ease;
                                    &:hover {
                                        background-color: #e30006;
                                    }
                                    &.active {
                                        background-color: #b00005;
                                    }
                                }
                            }
                            .slider-inner {
                                width: 100%;
                                height: 100%;
                                position: relative;
                                .item {
                                    width: 100%;
                                    height: 100%;
                                    float: left;
                                }
                                .img {
                                    background-position: center top !important;
                                    width: 100%;
                                    height: 100%;
                                }
                            }
                            .slider-control {
                                position: absolute;
                                width: 30px;
                                height: 30px;
                                z-index: 999;
                                border-radius: 30px;
                                text-align: center;
                                font-weight: 900;
                                font-size: 20px;
                                line-height: 30px;
                                background-color: #ffffff;
                                opacity: 0.5;
                                cursor: pointer;
                                top: 40%;
                                box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.5);
                                transition: all 320ms ease;
                                &:hover {
                                    opacity: 0.65;
                                    background-color: #b00005;
                                }
                                &:active {
                                    opacity: 0.85;
                                }
                                &.prev {
                                    display: none;
                                    left: 20px;
                                }
                                &.next {
                                    display: none;
                                    right: 20px;
                                }
                            }
                            &:hover .slider-control {
                                &.prev {
                                    display: block;
                                    left: 20px;
                                }
                                &.next {
                                    display: block;
                                    right: 20px;
                                }
                            }
                        }
